<template>
  <div id="transfer-legal" v-if="coins.length">
    <div class="title">{{$t('asset.transfer')}}</div>
    <ul class="coins">
      <li
        v-for="(coin,index) in coins"
        :key="index"
        :class="['pointer whites',{'active':index == coinIndex}]"
        @click="coinIndex = index;number = ''"
      >{{coin.currency_name}}</li>
    </ul>
    <div class="types">
      <div class="types-l">
        <div>{{$t('asset.from')}}</div>
        <div>{{$t('asset.to')}}</div>
      </div>
      <div class="types-r">
        <div>
          <div>{{types[0]}}</div>
          <div>{{types[1]}}</div>
        </div>
        <img src="../../../static/imgs/transfer1.png" alt @click="types.reverse();number=''" />
      </div>
    </div>
    <div class="number">
      <input type="number" class="bdb input_color" :placeholder="$t('asset.pNum')" v-model="number" />
      <span style="color:#61688a">{{coins[coinIndex].currency_name}}</span>
      <span class="all" @click="all">{{$t('td.all')}}</span>
    </div>
    <div
      class="balance"
      style="line-height:60px;margin:20px 0 0px"
    >{{$t('asset.leverUse')}}：{{lever_coins[coinIndex].lever_balance}}</div>
    <div
      class="balance"
      style="margin:5px 0 30px"
    >{{$t('asset.legalUse')}}：{{coins[coinIndex].legal_balance}}</div>
    <button type="button" class="transfer" @click="transfer">{{$t('asset.transfer')}}</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      token: "",
      selectedCoin: { name: "", id: "" },
      lever_coins: [],
      coins: [],
      coinIndex: 0,
      types: [this.$t("asset.lever_account"), this.$t("asset.fince_account")],
      number: ""
    };
  },
  created() {
    this.getCoins();
  },
  methods: {
    getCoins() {
      this.token = window.localStorage.getItem("token") || "";
      this.$http({
        url: "/api/wallet/list",
        method: "post",
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok") {
          var legData = res.data.message.legal_wallet.balance;
          this.coins = legData.filter(function(item, index) {
            return item.is_lever == 1;
          });
          console.log(this.lever_coins);
          var data = res.data.message.lever_wallet.balance;
          this.lever_coins = data.filter(function(item, index) {
            // console.log(item)
            return item.is_legal == 1;
          });
          console.log(this.lever_coins);
        }
      });
    },
    all() {
      if (this.types[0] == this.$t("asset.lever_account")) {
        this.number = this.lever_coins[this.coinIndex].lever_balance;
      } else {
        this.number = this.coins[this.coinIndex].legal_balance;
      }
    },
    transfer() {
      if (this.number == "") {
        layer.msg(this.$t("asset.pNum"));
        return;
      } else {
        let data = {};
        data.number = this.number;
        data.type = this.types[0] == this.$t("asset.lever_account") ? 2 : 1;
        data.currency_id = this.coins[this.coinIndex].currency;

        // this.$http({
        //     url:'/api/wallet/change',
        //     method:'post',
        //     data:data,
        //     headers: { 'Authorization': this.token }
        // }).then(res => {
        //     if(res.data.type == 'ok'){
        //         layer.msg(res.data.message)
        //         setTimeout(() => {
        //             this.$router.push({path:'/legalAccount',query:{currency_id:this.coins[this.coinIndex].currency}})
        //         },2000)
        //     }else{
        //         layer.msg(res.data.message)
        //     }
        // })
        this.$http({
          url: "/api/wallet/transfer",
          method: "post",
          data: {
            wallet_id: this.coins[this.coinIndex].id,
            number: this.number,
            from: this.types[0] == this.$t("asset.lever_account") ? "lever" : "legal",
            to: this.types[0] == this.$t("asset.lever_account") ? "legal" : "lever"
          },
          headers: { Authorization: this.token }
        }).then(res => {
          if (res.data.type == "ok") {
            layer.msg(res.data.message);
            setTimeout(() => {
              // this.$router.push({
              //   path: "/legalAccount",
              //   query: { currency_id: this.coins[this.coinIndex].currency }
              // });
              this.$router.push({
                path: "/finance"
              });
            }, 1000);
          } else {
            layer.msg(res.data.message);
          }
        });
      }
    }
  }
};
</script>

<style lang='scss' scoped>
#transfer-legal {
  padding: 0 30px;
  color: #c7cce6;
  > .title {
    padding: 20px 0;
    font-size: 24px;
    color: #61688a;
  }
  > .coins {
    display: flex;
    justify-content: center;
    li {
      margin-right: 20px;
      padding: 5px 20px;
      //   color: #fff;
      //   background: #234165;
    }
    .active {
      background: #7a98f7 !important;
      color: #fff !important;
    }
  }
  > .types {
    margin: 30px 0;
    //   width: 400px;
    height: 80px;
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    .types-r {
      display: flex;
    }
    img {
      margin: 25px;
      width: 30px;
      height: 30px;
    }
  }
  .number {
    margin: 30px 0 0;
    //   width: 430px;
    line-height: 40px;
    input {
      // border:none;
      // color:#f2f5ff;
      background: none;
      line-height: 40px;
      width: 755px;
      // border-bottom: 2px solid #3a4975;
    }
    .all {
      margin-left: 10px;
      padding-left: 10px;
      border-left: 1px solid #ccc;
    }
  }
  .transfer {
    display: block;
    margin: 0 auto;
    line-height: 40px;
    width: 200px;
    background: #7a98f7;
    color: #fff;
    border: none;
  }
}
</style>



